<template>
    <div>
        <div class="bla">
            <img src="../static/img/后台管理系统（登陆页面）.jpg" alt="">
			<a href="#" style="background:url(../static/img/后台管理系统（登陆页面）.jpg) no-repeat;background-position: center;"></a>
		</div>
		<div class="login">
			<div class="login_logo">
					<img src="../static/img/logo.png" >
			</div>
			<el-form :rules="rules" :model="ruleForm" ref="ruleForm">
				<div class="login_center">
					<div class="text">
						<img src="../static/img/用户 (3).png" alt=""><p>|</p>					
							<el-form-item prop="adminName">
								<el-input v-model="ruleForm.adminName" type="text" name="" id="text" placeholder="请输入账号" style="outline:none" />
							</el-form-item>
					</div>	
					<div class="password">
						<img src="../static/img/密码 (1).png" alt=""><p>|</p>
						<el-form-item prop="adminpassword">
						<el-input v-model="ruleForm.adminpassWord" type="password" name="" id="password" placeholder="请输入密码" style="outline:none"/>
						</el-form-item>
					</div>	
				</div>
				<div class="login_bottom">
					<div class="remember">
						<div class="box">
							<img src="../static/img/对勾 (1).png" alt="">
						</div>
						<span>记住密码</span>
					</div>
					<div class="login_b">
						<el-form-item>
							<el-button type="success" class="login_button" :loading="loading" @click="submitForm" >登录</el-button>
						</el-form-item>
					</div>
				</div>
			</el-form>
		</div>
    </div>
</template>
<script>
export default {
	name:"login",
	data () {
		return {
			loading:false,
			ruleForm:{
				adminName:"",
			    adminpassWord:"",
			},
			rules:{
				adminName:[
					 { required: true, message: '请输入账号', trigger: 'blur' },
                     { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
				],
				adminpassWord:[
					 { required: true, message: '请输入密码', trigger: 'blur' },
                     { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
				]

			}
		}
	},
	methods: {
		submitForm(){
			this.$refs.ruleForm.validate(valid=>{
				if(valid){
					this.loading = true;
					this.$store.dispatch("adminLogin",{
						data:{
							adminName:this.ruleForm.adminName,
                            passWord:this.ruleForm.adminpassWord,
						},
						success:()=>{
							this.loading = false
						}
					})	
				}else{
					 this.$message({
					showClose: true,
					message: '您输入的账号有误',
					type: 'error'
					});
				}
			})
		}
	}
    
}
</script>
