import axios from "axios"
import bus from '../../bus/index'
export default {
    state: {
          
    },
    mutations: {
       
        
     
    },
    getters: {
        
    },
    actions: {
         getPetFood({commit},id){
            axios.get("/findById",{
                params:{
                    productId:id
                }
            })
            .then(data=>{
                console.log(data);
                commit("SET_ADMIN_LOG",data.adminLog);
                commit("CHANGE_PAGE",{
                    pageIndex:data.pageIndex,
                    pageSum:data.pageSum
                })
            })
        },
        getAdminLogdDelete({commit},_id){
            axios.delete("http://127.0.0.1/adminLog",{
                params:{
                    _id
                }
            })
            .then(data=>{
                console.log(data)
                if(data.ok === 1){
                    console.log(data);
                    axios.get("http://127.0.0.1/adminLog",{
                params:{
                    pageIndex:1
                }
            })
            .then(data=>{
                commit("SET_ADMIN_LOG",data.adminLog);
                commit("CHANGE_PAGE",{
                    pageIndex:data.pageIndex,
                    pageSum:data.pageSum
                })
            })
                   
                }else{
                    bus.loading = false;
                    bus.$message({
                        showClose: true,
                        message:data.msg,
                        type: 'error'
                    });
                }
             
            }) 
        }     
    },

}